export default {
	data() {
		return {
			newsMy:''
		}
	},
	onLoad() {
		this.newsIndex()
	},
	methods: {
		async newsIndex (){	
				const that = this
				const res = await this.$myRequest({
					method:"POST",
					url:"/myMessage"
				})
				that.newsMy = res.data.data	
		},
		myNews(id){
			uni.navigateTo({
